// 
.hide { display: none; }
.unvisible { visibility: hidden; }
.clearfix { .clearfix(); }

// 
.@{ns}a-line { .text-hide(); }
.@{ns}a-line-ellipsis { .text-hide-ellipsis(); }

/*
 *@ Name:       inline-block 
 *@ Usage:       dib-boxdib 
 *@ Url:        http://thx.github.io/cube/doc/layout
 */

.dib {
    display: inline-block;
}

.dib-box {
    font-size: 0;
}

.dib-box .dib {
    vertical-align: top;
    font-size: @default-font-size;
    letter-spacing: normal;
    word-spacing: normal;
    line-height: inherit;
}

/*
 *@ Name:        
 *@ Rely        inline-block 
 *@ Usage:        justify dib, dib  justify 
 *@ Demo:        http://jsbin.com/OsEcOMA/1/
 */

/**
 * 
 * 
 */

.justify,
.dib-box .justify {
    font-size: 0;
    line-height: inherit;
}

.justify {
    text-align: justify;
    text-justify: inter-ideograph;
    -moz-text-align-last: justify;
    text-align-last: justify; /* IE 8+ */
}

/**
*  text-align-last 
* 1. margin-left:100%  WebKit 
*/
.justify:after {
    content: "";
    display: inline-block;
    width: 100%; /* 1 */
}

/*  */
.justify .dib {
    -moz-text-align-last: auto;
    text-align-last: auto;
    text-justify: auto;
    text-align: left;
    font-size: @default-font-size;
    letter-spacing: normal;
    word-spacing: normal;
}

/*
 *@ Name:       
 *@ Usage:      
 */

/*  inline-block  */
.center-box {
    font-size: 0;
    height: 100%; /*  */
}

/* 100% */
.center-box .center-hack {
    display: inline-block;
    font-size: 0;
    width: 0;
    height: 100%;
    vertical-align: middle;
}

.center-box .center-body {
    letter-spacing: normal;
    word-spacing: normal;
    display: inline-block;
    font-size: @default-font-size;
    vertical-align: middle; /*  */
    padding: 0 !important; /*  */
    margin: 0 !important;
    width: 100%; /*  */
    white-space: normal; /*  */
    word-wrap: break-word;
}

.center-box .center-img {
    display: inline-block;
    width: 100%;
    text-align: center; /*  */
    vertical-align: middle;
    padding: 0 !important; /*  */
    margin: 0 !important;
    font-size: 0;
}

.center-box img {
    vertical-align: middle; /*  img  */
}

/**
 * <select>
 * 1.  WebKit 
 * 2. Firefox bug
      https://bugzilla.mozilla.org/show_bug.cgi?id=649849
 * 3. Firefox for Android text-indent  5px
 * @doc: https://gist.github.com/joaocunha/6273016
 * @demo: http://jsbin.com/EdiZuvO/1/edit
*/
.select-no-appearance {
    -webkit-appearance: none; /* 1 */
    -moz-appearance: none; /* 2 */
    text-overflow: ""; /* 3 */
    text-indent: .01px; /* 3 */
}

/*
 * 1.  OS X  Webfont 
 * 2. Fiefox 25 -moz-osx-font-smoothing:auto()|grayscale
 * Demo: http://jsbin.com/iWItiQe/2
 */
.antialiased {
    -webkit-font-smoothing: antialiased; /* 1 */
    -moz-osx-font-smoothing: grayscale; /* 2 */
}

/**
 * &yen;()
 */
.rmb,
.yuan {
    font-family: arial, serif;
    font-style: normal;
}
